    top: 0;
}

.vertical .tab-container {
    min-height: 200px;
    border-radius: 0 5px 5px 0;
}
.tab-bar-item {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 0;
    overflow: hidden;
}

.tab-bar-item.active {
    margin: 0;
    padding: 0;
    width: 100%;
    height: auto;
}
.input {
    box-sizing: border-box;
    width: 100%;
    padding: 5px 15px 5px 40px;
    color: #b2b2b2;
    font-size: 14px;
    border: 1px solid #e5e5e5;
    margin-bottom: 20px;
}
.input:focus {
    border: 1px solid #e5e5e5;
    outline: 1px solid #e5e5e5;
}
.input[disabled] {
    cursor: not-allowed;
}

.input[name="checkbox"],
.input[name="radio"] {
    width: 5px;
    height: 5px;
}

.input.error {
    border-color: #e44d3a;
    background-color: rgba(212, 55, 40, 0.2);
    outline: #e44d3a;
}

.inputWrapper {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    position: relative;
}

.labelsWrapper {
    margin-bottom: 5px;
    display: flex;
    justify-content: space-between;
}

.inputRequired {
    color: #e44d3a;
}

.inputRequired,
.inputLabel {
    font-weight: 600;
    font-size: 10px;
    text-transform: uppercase;
}

.inputError {
    margin-top: 5px;
    color: #e44d3a;
    font-size: 12px;
}
.btn
{
    color: #404040;
    font-size: 14px;
    font-weight: 500;
    background-color: #e5e5e5;
    display: flex;
    justify-content: center;
    align-items: center;
}
.btn
{
    border: none;
    outline: none;
}
.btn.active
{
    color: #ffffff;
    background-color: #e44d3a;
    outline: none;
    border: none;
}
.btn.facebook
{
    width: 100%;
    color: #ffffff;
    font-size: 15px;
    font-weight: 500;
    padding: 10px 0;
    background-color: #3b5999;
    margin-bottom: 20px;
}
.btn.twitter
{
    width: 100%;
    color: #ffffff;
    font-size: 15px;
    font-weight: 500;
    padding: 10px 0;
    background-color: #55acee;
}
.checkboxContainer
{
    margin-top: 10px;
}
.checkboxContainer label
{
    position: relative;
}


form
